<template lang="html">
  <div id="dashboard">
    <table class="bordered striped">
        <thead>
          <tr>
              <th>Employee Id</th>
              <th>Name</th>
              <th>Department</th>
              <th>Position</th>
              <th>Actions</th>
          </tr>
        </thead>

        <tbody v-for="emp in employee" :key="emp.id">
          <tr>
            <td>{{ emp.emp_id }}</td>
            <td>{{ emp.name }}</td>
            <td>{{ emp.dept }}</td>
            <td>{{ emp.position }}</td>
            <td>
              <router-link class="btn" :to="{ name: 'view-employee', params: {emp_id: emp.emp_id} }"><i class="fa fa-eye"></i></router-link>
            </td>
          </tr>
        </tbody>
      </table>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-small red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      employee: []
    }
  },
  created () {
    db.collection('employee').orderBy('dept').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        // console.log(doc.data());
        const data = {
          'id': doc.id,
          'emp_id': doc.data().emp_id,
          'name': doc.data().name,
          'dept': doc.data().dept,
          'position': doc.data().position
        }
        this.employee.push(data)
      })
    })
  }
}
</script>

<style lang="css">
</style>



