<template lang="html">
  <div id="view-employee" class="">
    <div class="row">
        <div class="col s12 m8">
          <router-link class="btn" to="/"><i class="fa fa-arrow-left"></i></router-link>
          <div class="card blue-grey darken-1">
            <div class="card-content white-text">
              <span class="card-title">Employee ID: {{emp_id}}</span>
              <span class="card-title">Name :{{name}}</span>
              <h4>Postion: {{position}}</h4>
              <h5>Department: {{dept}}</h5>
            </div>
            <div class="card-action">
              <router-link class="btn green" :to="{ name: 'edit-employee', params: {empployee_id: emp_id} }">Edit</router-link>
              <button @click="deleteEmp" class="btn red">delete</button>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-employee',
  data () {
    return {
      emp_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employee').where('emp_id', '==', to.params.emp_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.emp_id = doc.data().emp_id
            vm.name = doc.data().name
            vm.dept = doc.data().dept
            vm.position = doc.data().position
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employee').where('emp_id', '==', this.$route.params.emp_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.emp_id = doc.data().emp_id
            this.name = doc.data().name
            this.dept = doc.data().dept
            this.position = doc.data().position
          })
        })
    },
    deleteEmp () {
      if (confirm('Are you sure to delete Employee')) {
        db.collection('employee').where('emp_id', '==', this.$route.params.emp_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push('/')
            })
          })
      }
    }
  }
}
</script>

<style lang="css">
</style>



